.footer {
  text-align: center;
}

.footer__text span {
  color: var(--first-color);
}
.footer__text {
  font-size: var(--h3-font-size);
  color: var(--title-color);
  font-weight: var(--font-semibold);
}

.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.footer__link {
  color: var(--title-color);
  display: flex;
  gap: 0.5rem;
  align-items: center;
  transition: .3s;
}

.footer__link:hover {
  color: hsl(43, 100%, 68%);
}